<template>
  <div class="project">
    <div class="top">
      <div class="map">
        <p class="stitle">项目区位分布</p>
        <div class="map-container" id="chart_projects_dist"></div>
      </div>
      <div class="edata">
        <p class="stitle">项目数量分布</p>
        <div id="echarts-project"></div>
      </div>
    </div>
    <div class="edata-z">
      <p class="stitle">项目新增趋势</p>
      <div id="echarts-tredent"></div>
    </div>
  </div>
</template>

<script>
import { getItem } from '@/utils/funcs'
import chartUtil from '@/utils/chart_util'
import constant from '@/common/constant'
import { projectDist } from '@/api/dataCenter'

export default {
  name: 'Project',
  mounted() {
    this.loadProjectsDistData()
    this.drawProject()
    this.drawTredent()
  },
  data() {
    return {
      trendDays: 30,
      trendDaysDefined: {
        start: 0,
        end: 0
      },
      devicesTotal: 0,
      chartAlarmTimes: undefined,
      chartProjectDistNum: undefined,
      chartDeviceDistRate: undefined,
      chartProjectsDist: undefined,
      chartNewTrend: undefined
    }
  },
  methods: {
    loadProjectsDistData() {
      const myChartProject = this.$echarts.init(document.getElementById('chart_projects_dist'));
      projectDist().then(resp => {

        // if (!this.chartProjectsDist) {
        //   this.chartProjectsDist = this.$echarts.init(document.getElementById('chart_projects_dist'))
        // }
        // if (!this.chartProjectDistNum) {
        //   // this.chartProjectDistNum = this.$echarts.init(document.getElementById('chart_project_dist_num'))
        // }
        this.devicesTotal = 0
        const category = []
        const numData = [{
          name: '项目数量',
          data: []
        }]
        resp.data.forEach(i => {
          category.push(i.region)
          numData[0].data.push(i.count)
          this.devicesTotal += i.count
        })
        let center = '北京市'
        let zoom = 5
        if (getItem(constant.city)) {
          center = getItem(constant.city)
          zoom = 12
        } else if (getItem(constant.province)) {
          center = getItem(constant.province)
          zoom = 8
        }
        // this.chartProjectsDist.setOption(chartUtil.mapChartOpt(resp.data, 'region', 'count'))
        // this.chartProjectsDist.setOption(chartUtil.bmapChartOpt(resp.data, zoom, center, 'region', 'count'))
        myChartProject.setOption(chartUtil.bmapChartOpt(resp.data, zoom, center, 'region', 'count'))
        console.log(myChartProject)
        // this.chartProjectDistNum.setOption(chartUtil.yBarChartOpt(category, numData))
      })
    },
    drawProject() {
      const myChartProject = this.$echarts.init(document.getElementById('echarts-project'));
      const seriesLabel = {
        normal: {
          show: true,
          textBorderColor: '#14a0e6', //
          textBorderWidth: 2
        }
      }
      const option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        // legend: {
        //   data: ['项目数量']
        // },
        grid: {
        },
        toolbox: {
        },
        xAxis: {
          type: 'value',
          name: '项目总数',
          axisLabel: {
            formatter: '{value}'
          },
          splitLine: {
            show: true,
            lineStyle: {
              // 使用深浅的间隔色
              color: ['#333']
            }
          },
          axisLine: {
            lineStyle: {
              color: '#b3b5b7'
            }
          },
          nameTextStyle: {
            color: '#14a0e6',
            verticalAlign: 'top'
          }
        },
        yAxis: {
          type: 'category',
          inverse: true,
          data: ['湖北省'],
          axisLine: {
            lineStyle: {
              color: '#b3b5b7'
            }
          }
        },
        series: [
          {
            name: '项目数量',
            type: 'bar',
            data: [1],
            label: seriesLabel,
            itemStyle: {
              normal: {
                color: '#69ccc7'
              }
            }
          }
        ]
      };
      myChartProject.setOption(option);
    },
    drawTredent() {
      const myChartTredent = this.$echarts.init(document.getElementById('echarts-tredent'));
      const option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        xAxis: {
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
          axisLine: {
            lineStyle: {
              color: '#69ccc7'
            }
          }
        },
        yAxis: {
          type: 'value',
          splitLine: {
            show: true,
            lineStyle: {
              // 使用深浅的间隔色
              color: ['#70d9f9']
            }
          },
          axisLine: {
            lineStyle: {
              color: '#72a7c4'
            }
          }
        },
        series: [{
          data: [820, 932, 901, 934, 1290, 1330, 1320],
          type: 'line',
          smooth: true,
          itemStyle: {
            normal: {
              color: '#70d9f9',
              lineStyle: {
                color: '#70d9f9'
              }
            }
          }
        }]
      };
      myChartTredent.setOption(option);

    }
  }
}
</script>

